import React from 'react'
import DateData from './DateData';
import MyFavorites from './MyFavorites'
import { useSelector } from "react-redux";
import { useDispatch } from 'react-redux';

import { setSearch, setQuery } from '../actions'
import { Link } from 'react-router-dom';
import FilledInput from '@material-ui/core/FilledInput';
import Button from '@material-ui/core/Button';
import logo from '../images/logo2.png'




export const Header = () => {
    const search = useSelector(state => state.search.search)
    const dispatch = useDispatch();

    const getSearch = (e) => {
        e.preventDefault();
        if (search !== '') {
            dispatch(setQuery(search))
        }
    }
    return (
        <div>
            <header className="app-header">
                <div className="header-logo-favorites">
                    <MyFavorites />
                    <Link to='/'><img className="logo" src={logo}></img></Link>
                </div>
                <form onSubmit={getSearch} className="search-form">
                    <FilledInput className="search-bar" type="text" value={search} onChange={e => dispatch(setSearch(e.target.value))}></FilledInput>
                    <Button variant="contained" color="primary" type="submit" className="button-search" >
                        Search
                    </Button>
                </form>
                <DateData />
            </header>
        </div>
    )
}
export default Header;