import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import FavoriteRecipe from './FavoriteRecipe.js'
import Button from '@material-ui/core/Button';

const MyFavorites = () => {
    const list = useSelector(state => state.likesList.likes);
    const [activeSideBar, setActiveSideBar] = useState(false)


    const displayFavorites = () => {
        setActiveSideBar(!activeSideBar)
    }

    const getFavorites = () => {
        return list.length > 0 ? <div><ol>{list.map((recipe) => <FavoriteRecipe recipe={recipe} />)}</ol> </div> : <p>"no favorite yet"</p>
    }


    return (
        <div>
            <Button variant="contained" color="primary" className="favorites-button" onClick={displayFavorites}>
                <i class="clipboard icon"></i>
                <label>My Favorites</label>
            </Button>
            <div className={` ${activeSideBar ? 'sidebar-active' : ''}`}>
                {activeSideBar && <button className="close-button" onClick={() => setActiveSideBar(!activeSideBar)}><i class="close icon"></i></button>}
                {activeSideBar && getFavorites()}
            </div>
        </div >
    )
}

export default MyFavorites

